<h1 id="welcome" tabindex="-1"><a class="header-anchor" href="#welcome" aria-hidden="true">#</a> Welcome!</h1>
<p>Welcome to <em>Introduction to Script Programming</em>! In this course you'll learn how to create small programs in Python your computer can execute.</p>
<p>This website will be used to host the course material. Canvas will primarily be used to:</p>
<ul>
<li>Send information to you through announcements (be sure to have your global notification settings or your course notification settings enabled on Canvas)</li>
<li>Organize you into lab groups (see the People page on Canvas)</li>
<li>Keep track of your progress in the course (passed lab assignments) (see the Assignments page and the Grades page on Canvas)</li>
</ul>
