<h2 id="information-to-old-students" tabindex="-1"><a class="header-anchor" href="#information-to-old-students" aria-hidden="true">#</a> Information to old students</h2>
<p>If you're an old student that took this course a previous year but still have some examination tests left to pass you take those tests as they are described this year. The old Ping Pong event/old Canvas Course won't be used any more, so join this year's Canvas Course, so we can keep track of your progress in the course this year there. If you aren't registered for the course this year, ask the course manager to invite you to the Canvas Course by sending him an email message based on the template in <FigureNumber /> below with the topic <code>Invitation to Canvas Course</code> to <code>peter.larsson-green@ju.se</code>.</p>
